@import url(https://fonts.googleapis.com/css2?family=Fira+Code&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.loading {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    min-height: 100vh;
}
#outer {
    width: 100%;
    text-align: center;
  }
  
  #inner {
    display: inline-block;
  }

  #inner .intro {
    margin-top:100px;
  }

  #inner .intro .title {
    font-size: 35px;
    font-weight: 600;
  }

  #inner .intro .description {
    margin-top:60px;
    margin-bottom:80px;
    color: #666666;
    font-size: 25px;
    width:800px;
  }

  #outer .summary {
    width:100%;
    background-color: #fcfcfc;
    height:400px;
    border-top:1px solid lightgrey;
  }

  #outer .summary .blurb {
    font-size: 20px;
    margin-top:80px;
    line-height: 40px;
  }

  #outer .summary .features {
    display:inline-block;
    margin-top:80px;
    margin-left: 100px;
    margin-right: 100px;
  }

  #outer .summary .features .features-title {
    margin:0px;
    font-size:25px;
    font-weight: 600;
  }



  
#navbar {
    width:100%;
    height:80px;
    border-bottom:1px solid lightgrey;
}

#title {
    font-size: 40px;
    margin-top:20px;
}

#logo {
  vertical-align: middle;
  height: 60px;
  width: auto;
}
.feature-text {
    font-size:18px;
    color: #666666;
    /* border-bottom: 1px solid #666666; */
}

.examples {
    margin-top:50px;
    margin-bottom:80px;
    text-align: center;
    width:800px;
    border:1px solid lightgrey;
    border-radius: 10px;
    box-shadow: 0 10px 30px #949fb0;
    overflow:hidden;
}

.examples .link {
    /* height:53px; */
    background-color: whitesmoke;
    border-bottom:1px solid lightgrey;
    border-top:1px solid lightgrey;
    font-family: 'Fira Code', monospace;
}

.examples .json {
    padding:20px;
    text-align: left;
    height:600px;
    overflow:auto;
}

.get-request {
    /* margin-top:0px; */
    margin-top:-24px;
    /* display:inline; */
}
.loading-circle {
    margin-top:200px;
    display: flex;
    justify-content: center;
}
.button {
    height:25px;
    width:40px;
    border-radius: 5px;
    background-color: #64d190;
    margin-top:12.5px;
    margin-left:30px;
    vertical-align: middle;
    /* text-align: center; */
    /* display: inline-flex; */
}

.button .get-text {
    font-size: 12px;
    line-height:26px;
    text-align: center;
    color:white;
}
.footer {
    width:100%;
    background-color: #3c454d;
    height:350px;
  }

  .footer .footer-features1 {
    float:left;
    text-align:left;
    margin-top:40px;
    margin-left: 300px;
    color:white;
  }

  .footer .footer-features2 {
    float:right;
    text-align: right;
    margin-top:40px;
    margin-right: 300px;
    color:white;
  }

  .footer .footer-features-title {
    margin:0px;
    font-size:20px;
    /* font-weight: 600; */
  }

  .footer .info {
      color: #acadad;
      margin-top:5px;
      line-height: 40px;
  }

  .footer .info a {
    color: #acadad;
    text-decoration: none;
	transition: border-color 200ms ease, color 200ms ease;
  }

  .footer .info a:hover {
	border-bottom-color: #3277a8;
 	color: #3277a8;
}

.footer .info a:focus {
 	border-bottom-color: #3277a8;
 	color: #3277a8;
}



.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

